@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: "Montserrat";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  height: 100vh;
  background-image: url("assets/img/main.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.game-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

body {
  overflow: hidden;
}

.roulette-page {
  background-image: url("assets/img/background.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-color: black;
  background-size: contain;
}

.rotate-parent {
  perspective: 1920px;
}

.rotated-div {
  transform-origin: bottom center;
  transform: rotateX(40deg);
  transition: 1.2s;
}

.origin-div {
  transform-origin: bottom center;
  transform: rotateX(0deg);
  transition: 1.2s;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.red-cell::before {
  @apply bg-white absolute w-full h-full;
  content: "";
  clippath: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
}
.red-cell::after {
  @apply bg-red-700 absolute w-full h-full;
  content: "";
  clippath: polygon(50% 5%, 95% 50%, 50% 95%, 5% 50%);
}
.black-cell::before {
  @apply bg-white absolute w-full h-full; 
  content: "";
  clippath: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
}

.black-cell::after {
  @apply bg-green-700 absolute w-full h-full;
  content: "";
  clippath: polygon(50% 5%, 95% 50%, 50% 95%, 5% 50%);
}

.pentagon-button .pentagon::before {
  position: relative;
  content: "";
  @apply bg-white absolute w-full h-full top-0 left-0 z-10;
  clippath: polygon(50% 0%, 0% 50%, 50% 100%, 100% 100%, 100% 0%);
}

.pentagon-button .pentagon::after {
  position: relative;
  content: "";
  @apply bg-gray-700/70 absolute w-full h-full top-0 left-0 z-10;
  clippath: polygon(50% 1%, 1% 50%, 50% 99%, 99% 99%, 99% 1%);
}

.pentagon-button .pentagon-mask::before {
  position: relative;
  content: "";
  @apply bg-white absolute w-full h-full top-0 left-0 z-20;
  clippath: polygon(50% 0%, 0% 50%, 50% 100%, 100% 100%, 100% 0%);
}

.pentagon-button .pentagon-mask::after {
  position: relative;
  content: "";
  @apply bg-gray-800/90 absolute w-full h-full top-0 left-0 z-20;
  clippath: polygon(50% 1%, 1% 50%, 50% 99%, 99% 99%, 99% 1%);
}

.settings-container .nav-button {
  @apply w-full inline-flex items-center justify-center p-4 rounded-t-lg transition-colors duration-200;
}

.settings-container .nav-button.active {
  @apply text-[#FFD962] border-[#FFD962] border-b-4
}
.settings-container .nav-button.deactive {
  @apply text-white hover:text-[#FFD962] border-[1px]
}
